

.container-patch {
  width: 100%;
  margin: auto;
  background-color: #4c4f27;
  position: fixed;
  top: 0;
  height: 56px;
  z-index: 999999999999;
  transition: height 0.3s ease;
  /* margin-top: 50px; */
}
.wrapper {
  display: grid;
  grid-template-columns: 29% 29% 29% 12%;
  gap: 4px;
  width: 100%;
  height: 56px;
  justify-content: space-between;
  align-items: center;
  padding: 0px 4px;
}
.auto-search-name,
.calendr-room-book-div,
.input-count-all {
  /* margin: 10px; */
  background-color: white;
}

.patch-mainp-tag {
  font-size: 13px;
  line-height: 17px;
  font-weight: 600;
  font-family: Arial, Helvetica, sans-serif;
  margin: auto 0;
  margin-right: 80px;
}
.patch-mainp-tag span {
  font-size: 22px;
  font-weight: 600;
}


.input-count-second-div {
  position: absolute;
  top: 55px;
  background: rgb(255, 255, 255);
  min-width: 330px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 4px;
}
.count-main-cont {
  display: flex;
  padding: 8px 20px;
}
.select-p-mobile-view {
  width: 160px;
  margin: auto 0;
  text-align: left;
}
.desktop-room-counter {
  border: 1px solid rgb(161, 161, 161);
  padding: 8px 10px;
  border-radius: 6px;
}
.desktop-room-counter button {
  font-size: 11px;
  border: none;
  background: none;
  /* color: rgb(82, 151, 224); */
}
.span-count-all {
  font-size: 16px;
  padding: 0 30px;
}
.toggle-input-button {
  border: 1px solid rgb(71, 160, 233);
  color: rgb(64, 170, 212);
  background: #fff;
  width: 290px;
  padding: 8px 10px;
  font-weight: 600;
  border-radius: 4px;
  margin-bottom: 10px;
  margin-top: 40px;
}
.auto-search-name {
  display: flex;
  border-radius: 4px;
  padding: 11px;
}

.calendr-room-book-div {
  display: flex;
  border-radius: 4px;
}
.input-count-all {
  border-radius: 4px;
}
.calendar-icon-div {
  margin: auto 0px auto 10px;
}
.hotel-icon {
  margin: auto 10px auto 0px;
}
.person-icon-toggle-div {
  margin: auto 10px auto 0px;
}
.expand-icon-toggle-div {
  margin: auto;
  display: flex;
  align-items: center;
}
.input-p-all-content {
  display: flex;
  padding: 11px;
  align-items: center;
  font-size: 14px;
  font-weight: 500;
  line-height: normal;
}
.auto-search-hotel-name {
  font-size: 14px;
  font-weight: 500;
  line-height: normal;
}

/* ........................  */
@media (max-width: 850px) {
  .container-patch {
    width: 100%;
    border-radius: 4px;
    margin: auto;
    /* background-color: rgba(27, 100, 14, 0.94); */
    position: fixed;
    top: 0;
    height: 100vh;

    /* margin-top: 5px; */
  }
  .wrapper {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 8px;
    width: 100%;
    height: 56px;
    /* justify-content: space-evenly; */
    justify-content: space-between;
    align-items: center;
    /* background: rgb(25, 118, 210); */
    /* color: #ffffff; */
    padding: 150px 15px;
  }
  .input-count-second-div {
    position: absolute;
    top: 270px;
    background: rgb(255, 255, 255);
    min-width: 90%;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    border-radius: 4px;
  }
  .mobile-select-mob-view {
    border-radius: 4px;
    width: 100px;
    border: 1px solid rgb(168, 166, 166) !important;
    padding: 6px;
    text-align: center;
  }
  .toggle-input-button {
    border: 1px solid rgb(167, 167, 167);
    color: rgb(255, 255, 255);
    background: rgb(50, 143, 179);
    width: 290px;
    padding: 8px 10px;
    font-weight: 600;
    border-radius: 4px;
    margin-bottom: 10px;
    margin-top: 40px;
  }
  .count-main-cont {
    display: grid;
    padding: 8px 20px;
    grid-template-columns: 65% 30%;
  }
  .auto-search-name {
    display: flex;
    border-radius: 4px;
    padding: 7px 11px;
  }
  .input-p-all-content {
    display: flex;
    padding: 7px 11px;
    align-items: center;
  }
  .select-p-mobile-view {
    width: 154px;
    margin: auto 0;
    text-align: left;
  }
}

.book-now-button-mobile {
  display: none;
  position: fixed;
  bottom: 0px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #006fe6;
  color: white;
  padding: 8px 20px;
  /* border-radius: 5px; */
  cursor: pointer;
  z-index: 99999999999;
  width: 100%;
  font-size: 16px !important;
  font-weight: 500;
}

/* Show the "Book Now" button and adjust container behavior on mobile */
@media (max-width: 768px) {
  .book-now-button-mobile {
    display: block;
    z-index: 9999999999999;
  }
}
