* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.calendarWrap {
  /* display: inline-block; */
  position: relative;
  /* border: 1px solid red; */
  /* padding: 4px; */
  /* padding-right: 18px; */
  /* border-right: 2px solid rgb(233, 227, 227); */

  width: 100%;
  text-align: left;
}

.calendarElement {
  position: absolute;
  left: 45%;
  transform: translateX(-50%);
  top: 3rem;
  border: 1px solid #ccc;
  z-index: 9999999999999;
}

.rdrDefinedRangesWrapper {
  display: none;
}

.rdrMonth {
  width: 23.667em;
}
.calendar-popup {
  cursor: pointer;
  padding: 15.5px;
  font-size: 14px;
  font-weight: 500;
  line-height: normal;
  /* background: black;
  color: #fff;
  padding: 3px 6px;
  border-radius: 4px; */
}
.calendar-popup:hover {
  /* cursor: pointer;
  background: rgb(255, 255, 255);
  color: #000000;

  border-radius: 4px; */
}
.rdrMonthAndYearWrapper {
  align-items: center;
  height: 25px !important;
  padding-top: 10px;
}
.rdrDateDisplay {
  margin: 0 !important;
}
.rdrDateDisplayItem input {
  cursor: pointer;
  height: 1.5em !important;
  line-height: 1.5em;
  border: 0px;
  background: transparent;
  width: 100%;
  color: #849095;
}
.rdrDay {
  /* height: 2em !important; */
}
.rdrDayToday .rdrDayNumber span:after {
  /* bottom: 2px !important; */
}
@media (max-width: 650px) {
  .calendarWrap {
    border: none;
    border: none;
    /* color: white; */
    font-weight: 600;
    letter-spacing: 1px;
  }

  .calendarElement {
    border: none;
    top: 0.5rem;
    left: 45%;
  }

  .rdrCalendarWrapper {
    color: #000000;
    font-size: 14px;
    width: 100%;
    margin: auto;
    height: 55vh;
    border-radius: 4px;
  }
  .rdrMonth {
    width: 22em;
  }
  .calendar-popup {
    cursor: pointer;
    padding: 11px;
  }
}
