.hero-btn {
  padding: 15.5px 0px;
  border: none;
  font-size: 16px;
  color: #fff;
  background: var(--button-color);
  background: rgba(35, 166, 221, 0.965);
  border-radius: 4px;
  width: 100%;
  font-weight: 500;
  font-family: Arial, Helvetica, sans-serif;
}
.hero-btn:hover {
  /* color: rgb(25, 118, 210); */

  /* background: rgb(255, 255, 255); */
}

@media (max-width: 650px) {
  .hero-btn {
    padding: 10px 45px;
    border: none;
    font-size: 14px;
    color: #fff;

    margin: auto;
    border-radius: 4px;
    margin-top: 10px;
  }
}
